// Config
$left-axis: 65px;
$tooltip-color: rgba(46, 85, 132, .9);

.formatted-duedate {
  overflow: visible !important;
}

// Original styles
button[data-balloon] {
  overflow: visible;
}

[data-balloon] {
  position: relative;
  cursor: pointer;
}

[data-balloon]:after {
  width: 130px;
  text-align: center;
  opacity: 0;
  pointer-events: none;
  transition: all .18s ease-out .18s;
  font-family: sans-serif !important;
  font-weight: normal !important;
  font-style: normal !important;
  text-shadow: none !important;
  font-size: 12px !important;
  background: $tooltip-color;
  border-radius: 4px;
  color: #fff;
  content: attr(data-balloon);
  padding: .5em 0; // Default: .5em 1em
  position: absolute;
  white-space: nowrap;
  z-index: 10;
}

[data-balloon]:before {
  background: no-repeat url('data:image/svg+xml;charset=utf-8, %3Csvg%20xmlns%3D%22http://www.w3.org/2000/svg%22%20width%3D%2236px%22%20height%3D%2212px%22%3E%3Cpath%20fill%3D%22rgba(46, 85, 132, .9)%22%20transform%3D%22rotate(0)%22%20d%3D%22M2.658, .000%20C-13.615, .000%2050.938, .000%2034.662, .000%20C28.662, .000%2023.035, 12.002%2018.660, 12.002%20C14.285, 12.002%208.594, .000%202.658, .000%20Z%22/%3E%3C/svg%3E');
  background-size: 100% auto;
  width: 18px;
  height: 6px;
  opacity: 0;
  pointer-events: none;
  transition: all .18s ease-out .18s;
  content: '';
  position: absolute;
  z-index: 10;
}

[data-balloon]:hover:before,
[data-balloon]:hover:after,
[data-balloon][data-balloon-visible]:before,
[data-balloon][data-balloon-visible]:after {
  filter: alpha(opacity=100);
  opacity: 1;
  pointer-events: auto;
}

[data-balloon].font-awesome:after {
  font-family: FontAwesome;
}

[data-balloon][data-balloon-break]:after {
  white-space: pre;
}

[data-balloon][data-balloon-blunt]:before,
[data-balloon][data-balloon-blunt]:after {
  transition: none;
}

[data-balloon][data-balloon-pos="up"]:after {
  bottom: 100%;
  left: $left-axis;
  margin-bottom: 6px;
  transform: translate(-50%, 10px);
  transform-origin: top;
}

[data-balloon][data-balloon-pos="up"]:before {
  bottom: 100%;
  left: $left-axis;
  margin-bottom: 0;
  transform: translate(-50%, 10px);
  transform-origin: top;
}

.actions button[data-balloon][data-balloon-pos="up"]:after,
.actions button[data-balloon][data-balloon-pos="up"]:before {
  left: 45%;
}

[data-balloon][data-balloon-pos="up"]:hover:after,
[data-balloon][data-balloon-pos="up"][data-balloon-visible]:after {
  transform: translate(-50%, 0);
}

[data-balloon][data-balloon-pos="up"]:hover:before,
[data-balloon][data-balloon-pos="up"][data-balloon-visible]:before {
  transform: translate(-50%, 0);
}

[data-balloon][data-balloon-pos="up-left"]:after {
  bottom: 100%;
  left: 0;
  margin-bottom: 11px;
  transform: translate(0, 10px);
  transform-origin: top;
}

[data-balloon][data-balloon-pos="up-left"]:before {
  bottom: 100%;
  left: 5px;
  margin-bottom: 5px;
  transform: translate(0, 10px);
  transform-origin: top;
}

[data-balloon][data-balloon-pos="up-left"]:hover:after,
[data-balloon][data-balloon-pos="up-left"][data-balloon-visible]:after {
  transform: translate(0, 0);
}

[data-balloon][data-balloon-pos="up-left"]:hover:before,
[data-balloon][data-balloon-pos="up-left"][data-balloon-visible]:before {
  transform: translate(0, 0);
}

[data-balloon][data-balloon-pos="up-right"]:after {
  bottom: 100%;
  right: 0;
  margin-bottom: 11px;
  transform: translate(0, 10px);
  transform-origin: top;
}

[data-balloon][data-balloon-pos="up-right"]:before {
  bottom: 100%;
  right: 5px;
  margin-bottom: 5px;
  transform: translate(0, 10px);
  transform-origin: top;
}

[data-balloon][data-balloon-pos="up-right"]:hover:after,
[data-balloon][data-balloon-pos="up-right"][data-balloon-visible]:after {
  transform: translate(0, 0);
}

[data-balloon][data-balloon-pos="up-right"]:hover:before,
[data-balloon][data-balloon-pos="up-right"][data-balloon-visible]:before {
  transform: translate(0, 0);
}

[data-balloon][data-balloon-pos='down']:after {
  left: $left-axis;
  margin-top: 11px;
  top: 100%;
  transform: translate(-50%, -10px);
}

[data-balloon][data-balloon-pos='down']:before {
  background: no-repeat url('data:image/svg+xml;charset=utf-8, %3Csvg%20xmlns%3D%22http://www.w3.org/2000/svg%22%20width%3D%2236px%22%20height%3D%2212px%22%3E%3Cpath%20fill%3D%22rgba(46, 85, 132, .9)%22%20transform%3D%22rotate(180 18 6)%22%20d%3D%22M2.658, .000%20C-13.615, .000%2050.938, .000%2034.662, .000%20C28.662, .000%2023.035, 12.002%2018.660, 12.002%20C14.285, 12.002%208.594, .000%202.658, .000%20Z%22/%3E%3C/svg%3E');
  background-size: 100% auto;
  width: 18px;
  height: 6px;
  left: $left-axis;
  margin-top: 5px;
  top: 100%;
  transform: translate(-50%, -10px);
}

[data-balloon][data-balloon-pos='down']:hover:after,
[data-balloon][data-balloon-pos='down'][data-balloon-visible]:after {
  transform: translate(-50%, 0);
}

[data-balloon][data-balloon-pos='down']:hover:before,
[data-balloon][data-balloon-pos='down'][data-balloon-visible]:before {
  transform: translate(-50%, 0);
}

[data-balloon][data-balloon-pos='down-left']:after {
  left: 0;
  margin-top: 11px;
  top: 100%;
  transform: translate(0, -10px);
}

[data-balloon][data-balloon-pos='down-left']:before {
  background: no-repeat url('data:image/svg+xml;charset=utf-8, %3Csvg%20xmlns%3D%22http://www.w3.org/2000/svg%22%20width%3D%2236px%22%20height%3D%2212px%22%3E%3Cpath%20fill%3D%22rgba(46, 85, 132, .9)%22%20transform%3D%22rotate(180 18 6)%22%20d%3D%22M2.658, .000%20C-13.615, .000%2050.938, .000%2034.662, .000%20C28.662, .000%2023.035, 12.002%2018.660, 12.002%20C14.285, 12.002%208.594, .000%202.658, .000%20Z%22/%3E%3C/svg%3E');
  background-size: 100% auto;
  width: 18px;
  height: 6px;
  left: 5px;
  margin-top: 5px;
  top: 100%;
  transform: translate(0, -10px);
}

[data-balloon][data-balloon-pos='down-left']:hover:after,
[data-balloon][data-balloon-pos='down-left'][data-balloon-visible]:after {
  transform: translate(0, 0);
}

[data-balloon][data-balloon-pos='down-left']:hover:before,
[data-balloon][data-balloon-pos='down-left'][data-balloon-visible]:before {
  transform: translate(0, 0);
}

[data-balloon][data-balloon-pos='down-right']:after {
  right: 0;
  margin-top: 11px;
  top: 100%;
  transform: translate(0, -10px);
}

[data-balloon][data-balloon-pos='down-right']:before {
  background: no-repeat url('data:image/svg+xml;charset=utf-8, %3Csvg%20xmlns%3D%22http://www.w3.org/2000/svg%22%20width%3D%2236px%22%20height%3D%2212px%22%3E%3Cpath%20fill%3D%22rgba(46, 85, 132, .9)%22%20transform%3D%22rotate(180 18 6)%22%20d%3D%22M2.658, .000%20C-13.615, .000%2050.938, .000%2034.662, .000%20C28.662, .000%2023.035, 12.002%2018.660, 12.002%20C14.285, 12.002%208.594, .000%202.658, .000%20Z%22/%3E%3C/svg%3E');
  background-size: 100% auto;
  width: 18px;
  height: 6px;
  right: 5px;
  margin-top: 5px;
  top: 100%;
  transform: translate(0, -10px);
}

[data-balloon][data-balloon-pos='down-right']:hover:after,
[data-balloon][data-balloon-pos='down-right'][data-balloon-visible]:after {
  transform: translate(0, 0);
}

[data-balloon][data-balloon-pos='down-right']:hover:before,
[data-balloon][data-balloon-pos='down-right'][data-balloon-visible]:before {
  transform: translate(0, 0);
}

[data-balloon][data-balloon-pos='left']:after {
  margin-right: 11px;
  right: 100%;
  top: 50%;
  transform: translate(10px, -50%);
}

[data-balloon][data-balloon-pos='left']:before {
  background: no-repeat url('data:image/svg+xml;charset=utf-8, %3Csvg%20xmlns%3D%22http://www.w3.org/2000/svg%22%20width%3D%2212px%22%20height%3D%2236px%22%3E%3Cpath%20fill%3D%22rgba(46, 85, 132, .9)%22%20transform%3D%22rotate(-90 18 18)%22%20d%3D%22M2.658, .000%20C-13.615, .000%2050.938, .000%2034.662, .000%20C28.662, .000%2023.035, 12.002%2018.660, 12.002%20C14.285, 12.002%208.594, .000%202.658, .000%20Z%22/%3E%3C/svg%3E');
  background-size: 100% auto;
  width: 6px;
  height: 18px;
  margin-right: 5px;
  right: 100%;
  top: 50%;
  transform: translate(10px, -50%);
}

[data-balloon][data-balloon-pos='left']:hover:after,
[data-balloon][data-balloon-pos='left'][data-balloon-visible]:after {
  transform: translate(0, -50%);
}

[data-balloon][data-balloon-pos='left']:hover:before,
[data-balloon][data-balloon-pos='left'][data-balloon-visible]:before {
  transform: translate(0, -50%);
}

[data-balloon][data-balloon-pos='right']:after {
  left: 100%;
  margin-left: 11px;
  top: 50%;
  transform: translate(-10px, -50%);
}

[data-balloon][data-balloon-pos='right']:before {
  background: no-repeat url('data:image/svg+xml;charset=utf-8, %3Csvg%20xmlns%3D%22http://www.w3.org/2000/svg%22%20width%3D%2212px%22%20height%3D%2236px%22%3E%3Cpath%20fill%3D%22rgba(46, 85, 132, .9)%22%20transform%3D%22rotate(90 6 6)%22%20d%3D%22M2.658, .000%20C-13.615, .000%2050.938, .000%2034.662, .000%20C28.662, .000%2023.035, 12.002%2018.660, 12.002%20C14.285, 12.002%208.594, .000%202.658, .000%20Z%22/%3E%3C/svg%3E');
  background-size: 100% auto;
  width: 6px;
  height: 18px;
  left: 100%;
  margin-left: 5px;
  top: 50%;
  transform: translate(-10px, -50%);
}

[data-balloon][data-balloon-pos='right']:hover:after,
[data-balloon][data-balloon-pos='right'][data-balloon-visible]:after {
  transform: translate(0, -50%);
}

[data-balloon][data-balloon-pos='right']:hover:before,
[data-balloon][data-balloon-pos='right'][data-balloon-visible]:before {
  transform: translate(0, -50%);
}

[data-balloon][data-balloon-length='small']:after {
  white-space: normal;
  width: 80px;
}

[data-balloon][data-balloon-length='medium']:after {
  white-space: normal;
  width: 150px;
}

[data-balloon][data-balloon-length='large']:after {
  white-space: normal;
  width: 260px;
}

[data-balloon][data-balloon-length='xlarge']:after {
  white-space: normal;
  width: 380px;
}

@media screen and (max-width: 768px) {
  [data-balloon][data-balloon-length='xlarge']:after {
    white-space: normal;
    width: 90vw;
  }
}

[data-balloon][data-balloon-length='fit']:after {
  white-space: normal;
  width: 100%;
}
